.Album_Cont {
  width: 200px;
  height: 320px;
  overflow: hidden;
  /* border: 2px solid red; */
  margin-bottom: 7px;
}
.Album_card {
  display: flex;
  flex-direction: column;
  height: 90%;
  overflow: hidden;
  margin-bottom:10px;
  border-radius: 10px 10px 10px 10px;
}
.Album_card_image {
  width: 100%;
  height: 80%;
  /* object-fit: cover; */
}
.Album_card_image > img {
  width: 100%;
  height: 100%;
}
.Album_card_text {
  width: 100%;
  height: 20%;
  color: var(--white-color);
  position: relative;
  background-color: var(--white-color);
}
.Album_card_text > h3 {
  background-color: var(--black-color);
  position: absolute;
  left: 10px;
  top: 20px;
  padding-left: 5px;
  padding-right: 5px;
  border: 1px solid var(--black-color);
  border-radius: 65px;
  font-weight: 400;
  font-size: 15px;
}
.Album_title {
  ont-weight: 400;
  font-size: 14px;
}